<template>
  <div id="app">
    <div id="nav">
      <h2>需要快应用引擎>1040</h2>
      <label for="checkbox">暂不能检测Page内数据变动，是否打开自动刷新: {{ checked }}</label>
      <input type="checkbox" id="checkbox" @click="autoRefresh" v-model="checked">
      <div v-for="item in pages" :key="item.pageId">
        <h3>
          pageId: {{ item.pageId }}, name: {{ item.name }}, path: {{item.path}}
        </h3>
        <tree-view :data="item.data" :options="{rootObjectKey: 'data'}"></tree-view>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      checked: false,
      framework: '',
      checkEvent: undefined,
      toolkit: ''
    }
  },
  computed: {
    pages () {
      return this.$store.state.pages
    }
  },
  methods: {
    autoRefresh (e) {
      if (e.target.checked) {
        this.checkEvent = setInterval(() => {
          this.$store.dispatch('getPageData')
        }, 5000)
      } else {
        if (this.checkEvent) {
          clearInterval(this.checkEvent)
          this.checkEvent = undefined
        }
      }
    }
  },
  created () {
    this.$store.dispatch('getPageData')
  }
}
</script>

