import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    pages: []
  },
  mutations: {
    setPageData(state, pages) {
      state.pages = pages
    }
  },
  actions: {
    async getPageData({ commit }) {
      const r = await axios.get(`/pages`)
      let pages = r.data.pages
      if (pages.length > 0) {
        const ps = pages.map(async (v, i) => {
          const pageData = await axios.request({
            url: `/runtime`,
            headers: { message: `getPage(${pages[i].pageId}).vm._data` }
          })

          pages[i].data = pageData.data
        })
        await Promise.all(ps)
      }
      commit('setPageData', pages)
    }
  }
})

window.addEventListener(
  'message',
  event => {
    if (event.data && event.data.method && event.data.method.includes('Page')) {
      store.dispatch('getPageData')
    }
  },
  false
)

export default store
